@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,400;8..144,500;8..144,600&display=swap');
*::-webkit-scrollbar {
    width: 0px;
}

* {
    scrollbar-width: thin;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Flex', sans-serif;
    font-family: 'Poppins', sans-serif;
    /* SAMO sans-serif */
    scroll-behavior: smooth;
}

body {
    background-color: rgb(40, 40, 40);
    color: white;
    background-color: white;
    color: black;
}


/* NAVBAR */

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    flex-direction: row;
    margin: 2.5vh 0;
}

.navbar li {
    width: 33%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.navbar li ul {
    cursor: pointer;
    color: gray;
    margin: 0 1rem;
}

.navbar .logo {
    font-weight: bold;
    font-size: 2.25rem;
}

.header__image {
    background: url(https://images.unsplash.com/photo-1581655353466-d5ad6765dd37?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80) 50% 65%;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 75vh;
    position: relative;
}

.header__image__text {
    color: white;
    position: absolute;
    bottom: 40%;
    right: 10%;
}

.header__image__text h1 {
    font-size: 3.75rem;
    font-weight: 600;
}

.header__image__text p {
    font-size: 1.5rem;
}

.header__image__text button {
    color: white;
    background: transparent;
    border: 1px solid white;
    outline: none;
    padding: 0.75rem 2rem;
    margin-top: 4vh;
    font-size: 1.2rem;
    cursor: pointer;
    transition: .1s;
}

.header__image__text button:hover {
    color: #000;
    background-color: white;
}


/*  AUTUMN  */

.autumn__section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    height: 85vh;
    background-color: white;
    color: black;
}

.autumn__left__section {
    width: auto;
    text-align: left;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: baseline;
    margin: 0 auto;
}

.autumn__left__section h1 {
    font-size: 4.5rem;
    font-weight: 500;
    line-height: 95%;
    letter-spacing: -0.5px;
}

.autumn__left__section p {
    margin: 2.25rem 0;
    letter-spacing: 0;
    word-spacing: -0.2px;
    font-size: 1.025rem;
}

.autumn__left__section button {
    text-transform: uppercase;
    border: 2px solid black;
    padding: 0.75rem 2.5rem;
    margin: 0 0 2rem;
    background-color: transparent;
    font-weight: bold;
    cursor: pointer;
    transition: .15s ease-in-out;
}

.autumn__left__section button:hover {
    background-color: black;
    color: white;
}

.autumn__right__section {
    width: 50vw;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.item__image__one {
    background-image: url(https://images.unsplash.com/photo-1572251583906-511b87c865e7?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=688&q=80);
    height: 60vh;
    width: 20vw;
    background-size: cover;
    background-position: center;
    cursor: pointer;
    filter: grayscale(20%);
}

.item__image__one:hover,
.item__image__two:hover {
    filter: grayscale(0);
    filter: saturate(150%);
}

.item__image__two {
    background-image: url(https://images.unsplash.com/photo-1572251337078-1063752b2a35?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=688&q=80);
    height: 60vh;
    width: 20vw;
    background-size: cover;
    background-position: center;
    cursor: pointer;
    filter: grayscale(20%);
}

.item__description {
    margin-top: 0.25vh;
    display: flex;
    justify-content: space-between;
}

.item__name {
    font-weight: bold;
}

.fixed__image {
    background: url(https://images.unsplash.com/photo-1572251328450-19c5082bc582?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1469&q=80) 50% 70%;
    height: 65vh;
    background-size: cover;
    background-attachment: fixed;
}


/* CATEGORIES */

.section__categories {
    height: 90vh;
    color: black;
    background-color: rgb(240, 240, 240);
}

.categories__heading {
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    font-size: 2.6rem;
    padding: 2.5rem 0;
    line-height: 20vh;
}

.categories__container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    margin: 0 auto;
    width: 70vw;
    height: 50vh;
}

.categories__item__one {
    background: url(https://images.unsplash.com/photo-1523381210434-271e8be1f52b?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80) 50% 50% no-repeat;
    align-self: flex-start;
}

.categories__item__two {
    background: url(https://images.unsplash.com/photo-1581017316696-709bf1da2ed5?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80) 50% 50% no-repeat;
    align-self: flex-end;
}

.categories__item__three {
    background: url(https://images.unsplash.com/photo-1609587312208-cea54be969e7?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80) 50% 50% no-repeat;
    align-self: flex-start;
}

.categories__item__one,
.categories__item__two,
.categories__item__three {
    position: relative;
    filter: grayscale(35%);
    height: 20vw;
    width: 20vw;
    background-size: cover;
    transition: .25s;
}

.categories__item__one:hover,
.categories__item__two:hover,
.categories__item__three:hover {
    filter: grayscale(0%);
}

.item__payload {
    color: white;
    position: absolute;
    bottom: 0;
    left: 7%;
}

.item__payload p {
    font-size: 2.05rem;
    text-shadow: 1px 1px 1px black;
    margin-bottom: 0.5rem;
}

.item__payload button {
    color: white;
    text-transform: uppercase;
    border: 1px solid white;
    padding: 0.75rem 2.5rem;
    margin: 0 0 2rem;
    background-color: transparent;
    cursor: pointer;
    transition: .15s ease-in-out;
}


/*  ABOUT  */

.aboutCompany {
    height: 160vh;
    background-color: white;
    color: black;
}

.aboutCompany__image {
    background: url(https://images.unsplash.com/photo-1572251328767-e59f06f13ba1?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1469&q=80) 50% 70%;
    width: auto;
    height: 75vh;
    background-size: cover;
}

.aboutCompany__container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    width: 80vw;
    margin: 0 auto;
}

.aboutCompany__container article {
    width: 45vw;
}

.aboutCompany__description {
    width: 70%;
    display: flex;
    flex-direction: column;
}

.aboutCompany__description h1 {
    font-size: 4rem;
    font-weight: 500;
    margin-bottom: 5%;
}

.aboutCompany__description p {
    font-size: 1rem;
}

.aboutCompany__description a {
    color: black;
    margin-top: 5%;
}

.aboutComapny__item__image {
    background-image: url(https://images.unsplash.com/photo-1559697242-7c922c6198c6?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80);
    height: 90vh;
    width: 30vw;
    z-index: 5;
    margin-top: -10%;
}


/*  QUOTE  */

.quoteSection {
    text-align: center;
    background-color: #EFEBE8;
    padding: 7.5vh 0 7vh;
    margin: 5vh auto;
}

.quote {
    width: 80vw;
    margin: 0 auto;
    font-size: 2.25rem;
    color: black;
}

.quoteSection .author {
    font-size: 1rem;
    color: gray;
    margin-top: 4vh;
    padding-bottom: 2vh;
    font-weight: 500;
}


/*  INSTAGRAM  */

.instagramSection__placeholder {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    width: 67.5vw;
    margin: 15vh auto;
}

.instagramSection__image {
    background-image: url(https://images.unsplash.com/photo-1610222034376-8dd1149e0e60?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=764&q=80);
    width: 27.5vw;
    background-size: cover;
    height: 80vh;
    background-repeat: no-repeat;
    background-position: center;
}

.instagramSection__text {
    width: 25vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 28vh;
    justify-content: space-between;
    margin-right: 2.5vw;
}

.instagramSection__text h1 {
    font-size: 2.75rem;
    font-weight: 500;
    margin-bottom: 5%;
}

.instagramSection__text p {
    font-size: 1rem;
}

.instagramSection__text a {
    color: black;
    margin-top: 5%;
}

.instagramSection {
    margin-top: 20vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.instagramSection__followUs {
    text-align: center;
}

.instagramSection__followUs h1 {
    font-size: 3rem;
    font-weight: 500;
    word-spacing: 2px;
    letter-spacing: 1.2px;
}

.instagramSection__followUs button {
    word-spacing: 2px;
    font-size: 1.04rem;
    text-transform: uppercase;
    border: 1px solid black;
    padding: 0.8rem 3.25rem;
    margin: 4rem 0;
    background-color: transparent;
    cursor: pointer;
    transition: .15s ease-in-out;
}

.top__gallery {
    display: flex;
    width: 80vw;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.top_gallery_item1 {
    background: url(https://images.unsplash.com/photo-1589810635657-232948472d98?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80) 50% 50%;
    height: 17.5vw;
    width: 22.5vw;
    background-size: cover;
}

.top_gallery_item2 {
    background: url(https://images.unsplash.com/photo-1549439602-43ebca2327af?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80) 50% 50%;
    height: 17.5vw;
    width: 22.5vw;
    background-size: cover;
}

.bottom__gallery {
    width: 80vw;
    margin-top: 7.5vh;
    margin-bottom: 5vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.bottom__gallery_item1 {
    background: url(https://images.unsplash.com/photo-1653715782349-238a9211f73f?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80) 50% 50%;
    height: 17.5vw;
    width: 17.5vw;
    background-size: cover;
}

.bottom__gallery_item2 {
    background: url(https://images.unsplash.com/photo-1616837874254-8d5aaa63e273?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=856&q=80) 50% 50%;
    height: 17.5vw;
    width: 17.5vw;
    background-size: cover;
}

.bottom__gallery_item3 {
    background: url(https://images.unsplash.com/photo-1572635196237-14b3f281503f?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80) 50% 50%;
    height: 17.5vw;
    width: 17.5vw;
    background-size: cover;
}

.bottom__gallery_item4 {
    background: url(https://images.unsplash.com/photo-1563545712546-34de145f484c?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80) 50% 50%;
    height: 17.5vw;
    width: 17.5vw;
    background-size: cover;
}


/*  NEWSLETTER  */

.newsletter {
    background-color: #EFEBE8;
    padding: 25vh 0 8vh;
    margin-top: -15vh;
    text-align: center;
}

.newsletter p {
    font-size: 2.4rem;
}

#newsletterInput {
    margin-top: 4vh;
    width: 17.5vw;
    background: transparent;
    text-align: left;
    padding: 1rem 1.5rem 1rem 1rem;
    outline: none;
    border: 1px solid gray;
}

#submitButton {
    padding: 1rem;
    border: 1px solid gray;
    border-left: none;
    cursor: pointer;
}


/* FOOTER */

#main__footer {
    background-color: #FFFFFF;
}

#main__footer a {
    display: block;
    text-decoration: none;
    color: rgb(50, 50, 50);
    font-size: 1.5rem;
    margin: 7vh 0;
    text-align: center;
    font-weight: 500;
}

#main__footer .footer__links {
    padding-top: 5vh;
    width: 70vw;
    margin: 7.5vh auto 0;
    border-top: 2px solid lightgray;
    color: gray;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    flex-direction: row;
}

#main__footer ul {
    list-style: none;
}

#main__footer ul li {
    margin: 1vh 0;
    cursor: pointer;
}